'use client';

import { FaTwitter, FaLinkedin, FaFacebook } from 'react-icons/fa';

declare global {
  interface Window {
    gtag: (command: string, action: string, params: object) => void;
  }
}

interface SocialShareProps {
  url: string;
  title: string;
  description: string;
}

const trackShare = (platform: string, url: string) => {
  if (typeof window !== 'undefined' && window.gtag) {
    window.gtag('event', 'share', {
      method: platform,
      content_type: 'page',
      item_id: url,
    });
  }
};

export const SocialShare = ({ url, title, description }: SocialShareProps) => {
  const encodedUrl = encodeURIComponent(url);
  const encodedTitle = encodeURIComponent(title);
  const encodedDesc = encodeURIComponent(description);

  return (
    <div className="flex gap-4 items-center py-4">
      <a
        href={`https://twitter.com/intent/tweet?url=${encodedUrl}&text=${encodedTitle}`}
        target="_blank"
        rel="noopener noreferrer"
        className="text-[#1DA1F2] hover:opacity-80 transition-opacity"
        aria-label="Share on Twitter"
        onClick={() => trackShare('Twitter', url)}
      >
        <FaTwitter size={24} />
      </a>
      <a
        href={`https://www.linkedin.com/shareArticle?mini=true&url=${encodedUrl}&title=${encodedTitle}&summary=${encodedDesc}`}
        target="_blank"
        rel="noopener noreferrer"
        className="text-[#0077B5] hover:opacity-80 transition-opacity"
        aria-label="Share on LinkedIn"
        onClick={() => trackShare('LinkedIn', url)}
      >
        <FaLinkedin size={24} />
      </a>
      <a
        href={`https://www.facebook.com/sharer/sharer.php?u=${encodedUrl}`}
        target="_blank"
        rel="noopener noreferrer"
        className="text-[#4267B2] hover:opacity-80 transition-opacity"
        aria-label="Share on Facebook"
        onClick={() => trackShare('Facebook', url)}
      >
        <FaFacebook size={24} />
      </a>
    </div>
  );
};